:root{
    --facebook: #3b5999;
    --instagram:#e4405f;
    --twitter: #55acee;
}
.section__padding{
    padding: 4rem 4rem 0rem;
}
.footer{
    background-color: #121213;
}
.sb__footer{
    display: flex;
    flex-direction: column;
    padding: 15px;
}
.sb__footer p:hover{
    color: #fff;
}
.sb__footer-links{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
}
.sb__footer-links_div{
    margin: 20px 0;
    width: 150px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    color: white;
}
.sb__footer-links_div:nth-child(4) h4{
    text-align: center;
}
.footer a, .footer p{
    color: rgb(175, 175, 179);
    text-decoration: none;
}
.icon{
    width: 130px;
    margin: 5px auto 0 auto;
    display: block;
    border-radius: 25px;
}
.iconLista{
    border-radius: 25px;
    display: flex;
    justify-content: space-between;
    margin: auto;
    padding: 0;
}
.icon:hover a{
    filter: blur(2px)
}
.iconos{
    list-style: none;
    width: 40px;
    height: 40px;
    text-align: center;
    transition: 0.2s;
    border-radius: 50%;
}
.iconos a{
    border-radius: 50%;
    display: block;
    background: #2d2d32;
    text-decoration: none;
    color: rgb(255, 255, 255);
    line-height: 40px;
}
.iconos a:hover{
    transform: scale(1.07);
    box-shadow: 0 5px 5px 0px rgba(0, 0, 0, .4);
    filter: none;
    color: #fff;
}
.facebook:hover{
    background: var(--facebook);
}
.instagram:hover{
    background: var(--instagram);
}
.twitter:hover{
    background: var(--twitter);
}
.sb__footer-links_div h4{
    font-size: 20px;
    line-height: 17px;
    margin-bottom: 0.9rem;
}
.sb__footer-links_div p{
    font-size: 14px;
    line-height: 15px;
    margin: 0.5rem 0;
    cursor: pointer;
}
.sb__footer-below{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 0.2rem;
}
.sb__footer-below-links{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
.sb__footer-below-links a{
    width: 100%;
    max-width: 160px;
    margin-bottom: 30px;
}
.sb__footer .sb__footer-below-links p{
    font-size: 13px;
    line-height: 15px;
    font-weight: 600;
    text-align: center;
    margin: 0;
}
.footer hr{
    color: white;
    width: 100%;
}
.sb__footer-copyright p{
    width: 100%;
    text-align: center;
    font-size: 13px;
}
@media screen and (max-width:850px){
    .sb__footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
}
@media screen and (max-width:550px){
    .sb__footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }

    .sb__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }

    .sb__footer-below{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .sb__footer-copyright{
        text-align: center;
    }

    .sb__footer-below-links p{
        margin-left: 0rem;
        margin-top: 1rem;
    }
}
@media screen and (max-width:400px){
    .sb__footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }
}
